.login {
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, #35a6c3, #938571);
}
.login .login-header {
  height: 80px;
  background: rgba(21, 20, 13, 0.5);
  display: flex;
  align-items: center;
  color: #fff;
  justify-content: center;
  font: 2em sans-serif;
}
.login .login-content {
  width: 400px;
  height: 300px;
  background: #fff;
  margin: 18% auto;
  padding: 20px 40px;
}
.login .login-content h2 {
  font: 1.8em sans-serif;
  text-align: center;
  font-weight: 300;
}
.login .login-content .login-form {
  margin-top: 47px;
}
.login .login-content .login-form .login-form-button {
  width: 100%;
}
