.ant-layout {
  background: transparent;
}
.teacher-manager {
  display: flex;
}
.teacher-manager-card {
  flex: 1 1;
  min-width: 310px;
  margin: 10px;
  max-width: 500px;
  height: 220px;
  padding: 0 10px;
  box-shadow: 4px 4px 5px #888888;
}
.teacher-manager-card .card-top {
  height: 180px;
  width: 100%;
  display: flex;
  align-items: center;
}
.teacher-manager-card .card-top-text {
  flex: 1 1;
  text-align: center;
}
.teacher-manager-card .card-top-text .text-title {
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 24px;
  margin-bottom: 15px;
}
.teacher-manager-card .card-top-text .text-p {
  font-size: 16px;
}
.teacher-manager-card .card-bottom {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.teacher-manager-card .card-bottom a {
  flex: 1 1;
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  margin-bottom: 30px;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: #000;
  text-align: center;
}
